.hovereffect{
	width:100%;
	height:100%;
	float:left;
	overflow:hidden;
	position:relative;
	text-align:center;
	cursor:default;
	background:-webkit-linear-gradient(bottom left, #ff89e9 0%, #05abe0 100%);
	background:-o-linear-gradient(bottom left, #ff89e9 0%, #05abe0 100%);
	background:linear-gradient(to top right, #ff89e9 0%, #05abe0 100%);}	
.hovereffect .overlay{
	width:100%;
	height:100%;
	position:absolute;
	overflow:hidden;
	top:0;
	left:0;
	padding:3em;
	text-align:left;
}
.hovereffect img{
	display:block;
	position:relative;
	max-width:none;
	width:calc(100% + 60px);
	-webkit-transition:opacity 0.35s,-webkit-transform .45s;transition:opacity 0.35s,transform .45s;
	-webkit-transform:translate3d(-40px,0,0);transform:translate3d(-40px,0,0)
}
.hovereffect .overlay:before{
	position:absolute;
	top:20px;
	right:20px;
	bottom:20px;
	left:20px;
	border:1px solid #fff;
	content:'';
	opacity:0;
	filter:alpha(opacity=0);
	-webkit-transition:opacity 0.35s,-webkit-transform .45s;
	transition:opacity 0.35s,transform .45s;
	-webkit-transform:translate3d(-20px,0,0);
	transform:translate3d(-20px,0,0)}
.hovereffect:hover img{
	opacity:.6;
	filter:alpha(opacity=60);
	-webkit-transform:translate3d(0,0,0);
	transform:translate3d(0,0,0)}
.hovereffect:hover 
.overlay:before,
.hovereffect:hover a,
.hovereffect:hover p{opacity:1;
	filter:alpha(opacity=100);
	-webkit-transform:translate3d(0,0,0);
	transform:translate3d(0,0,0)}
.iconos:hover{
	-webkit-transition:all .5s ease;
	-moz-transition:all .5s ease;
	-o-transition:all .5s ease;
	-ms-transition:all .5s ease;
	transition:all .5s ease;
	-webkit-transform:rotate(360deg);
	-moz-transform:rotate(360deg);
	-o-transform:rotate(360deg);
	-ms-transform:rotate(360deg);
	transform:rotate(360deg)}

